body {
    background-color: #eee;
}

#root {
    border-radius: 3px;
    border: 1px solid #e5e5e5;
    margin: 15px;
    background-color: white;
}

.tab {
    padding: 25px;
    color: #333;
}

ul.inline {
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: center;
}

ul.inline li {
    display: inline-block;
    padding: 10px;
    border-bottom: 2px solid #eee;
    transition: all 0.5s;
    font-weight: 300;
    cursor: pointer;
    color: #aaa;
}

ul.inline .selected {
    border-bottom: 2px solid #337ab7;
    color: #444;
}
